<script>
import { TimelineLite, TweenMax, Back } from "gsap";

export default {
  name: "ButterFly",
  props: {
    msg: String
  },
  mounted() {
    function butterflySet() {
      const tl = new TimelineMax({
        repeat: 10,
        yoyo: true
      });
      tl.add("start");
      tl.to(
        "#butterfly .right-wing, #butterfly .left-wing",
        1,
        { scaleY: 0.95, scaleX: 0.95, rotation: 40 },
        "start"
      );

      tl.to(
        ".body, .head , #butterfly .right-side, #butterfly .left-side",
        1,
        { scaleY: 1.95, scaleX: 1.95, rotation: 40 },
        "start"
      );
    }
    butterflySet();

    //butterfly repeat
    function revolve() {
      let tl = new TimelineMax({
        repeat: 17,
        yoyo: true
      });

      tl.add("start");

      tl.to(
        "#buttterfly .right-side, #buttterfly .left-side",
        1,
        {
          scaleY: 10.75,
          rotation: -45,
          transformOrigin: "50% 90%"
        },
        "start"
      );

      tl.to(
        "#buttterfly .body, #buttterfly .head",
        1,
        {
          y: -20
        },
        "start"
      );
      return tl;
    }

    revolve();

    //To fly

    function flyIn() {
      let tl = new TimelineMax({
        repeat: 17,
        yoyo: true
      });

      tl.add("start2");

      tl.fromTo(
        "#butterfly",
        7,
        {
          x: -1000 + Math.random()
        },
        {
          bezier: {
            type: "soft",
            values: [
              { x: 60 * Math.random(), y: 80 },
              { x: 150, y: 30 },
              { x: 400 + Math.random() * 100, y: 320 * Math.random() + 50 },
              { x: 500, y: 320 * Math.random() + 50 },
              { x: 700, y: 100 },
              { x: 850, y: 500 }
            ]
          },
          ease: Sine.easeOut,
          scaleY: 0.75
        },
        "start2"
      );
      return tl;
    }
    flyIn();
  }
};
</script>

